import React from 'react';
import './index.css';
import ScoreBoard from './scoreboard.js';
import FootballField from './footballField.js';
import globe from './index.js';

//make defensive plays matter
//make choosing left or right take into account if you receive or kick off
//add: sacks, interceptions, fumbles
//add diversity to what can happen with plays: left/middle/right, deep/medium/short, going out of bounds/being tackled

const Ordinals = [
    "First",
    "Second",
    "Third",
    "Fourth",
];

class Game extends React.Component {
    constructor(props) {
	super(props);
	this.homeGame = props.homeGame;
	this.yourTeam = props.yourTeam;
	this.yourActivePlayers = props.yourDepthCharts;
	this.opposingTeam = props.opposingTeam;
	this.opposingActivePlayers = props.opposingDepthCharts;
	this.playFns = {
	    offense: {
		Pass: () => this.pickPlay("pass"),
		Run: () => this.pickPlay("run"),
		Punt: () => this.pickPlay("punt"),
		"Field Goal": () => this.pickPlay("fieldGoal"),
	    },
	    defense: {
		Pass: () => this.pickPlay("pass"),
		Run: () => this.pickPlay("run"),
		"Punt Return": () => this.pickPlay("punt"),
	    },
	    kickOff: {
		"Kick Off": () => this.pickPlay("kickOff"),
	    },
	    gameOver: {
		"Main Page": () => this.endGame(),
	    },
	    next: {
		Next: () => this.nextLog(),
	    },
	    startGame: {
		"Coin Flip": () => this.coinFlip(),
	    },
	    coinFlip: {
		Heads: () => this.chooseCoinFlip("heads"),
		Tails: () => this.chooseCoinFlip("tails"),
	    },
	    choose: {
		Receive: () => this.choosePrivilege("Receive"),
		Defer: () => this.choosePrivilege("Defer"),
		Left: () => this.choosePrivilege("Left"),
		Right: () => this.choosePrivilege("Right"),
	    },
	};
	this.actions = [];
	this.yourTeamChoices = [];
	this.opposingTeamChoices = [];
    }

    componentWillMount = () => {
	this.setState({
	    isKickOff: true,
	    driveTowards: true, // true for right side
	    down: 1,
	    quarter: 0,
	    time: 300,
	    lineOfScrimmage: 35,
	    firstDownLine: 45,
	    result: "",
	    yourScore: 0,
	    opposingScore: 0,
	    possession: true,//true if you have ball
	    yourPlay: "kickOff",
	    opposingPlay: "kickOff",
	    bonusToPlay: 0,
	}, () => this.startGame());
    }

    buttons = () => {	
	const type = this.state.quarter === 0 ? "startGame" :
		     this.state.quarter === .25 ? "coinFlip" :
		     this.state.quarter === .5 ? "choose" :
		     this.state.quarter === 5 ? "gameOver" :
		     this.state.isKickOff ? "kickOff" :
		     this.state.possession ? "offense" : "defense";
	return (
	    <>
		<div className='playButtons'>
		    {Object.keys(this.playFns[type]).map(play => {
			return (
			    <div
				className='button'
				key={play}
				onClick={() => {
				    this.playFns[type][play]();
				    if (type !== "gameOver" && type !== "next") {
					this.nextAction();
				    }
				}}
			    >
				{play}
			    </div>
			)
		    })}
		</div>			
	    </>
	);
    }
    
    addActionToFront(action) {
	this.actions.unshift(action);
    }

    addActionToBack(action) {
	this.actions.push(action);
    }

    nextAction() {
	//console.trace("i");
	if (this.actions.length > 0) {
	    this.actions.shift()();
	}
    }

    startGame() {
	const result = "Hi there folks, welcome to a game between the " + this.yourTeam.name + " and the " + this.opposingTeam.name + ". I'm Chris Collinsworth and I will be your announcer for this game. In just a second we will have the coin flip and the game will be getting underway right after that.";
	this.setState({result});
    }

    coinFlip() {
	const result = "It is now time for the coin flip. The " + (this.homeGame ? this.opposingTeam.name : this.yourTeam.name) + " will choose head or tails. ";
	if (this.homeGame) {
	    this.addActionToFront(() => this.chooseCoinFlip());
	}
	const quarter = this.homeGame ? .5 : .25;
	this.setState({result, quarter}, () => this.nextAction());
    }
    
    chooseCoinFlip(choice = Math.random() < .5 ? "heads" : "tails") {
	const flipResult = Math.random() < .5 ? "heads" : "tails";
	const winningTeam = this.homeGame ? choice === flipResult ? this.opposingTeam.name : this.yourTeam.name :
			    choice === flipResult ? this.yourTeam.name : this.opposingTeam.name;
	if (winningTeam === this.opposingTeam.name) {
	    this.addActionToFront(() => this.chooseForOtherTeam());
	}
	this.addActionToFront(() => this.setCoinFlipResult(choice, flipResult, winningTeam));
	this.setState({quarter: .5}, () => this.nextAction());
    }

    chooseForOtherTeam() {
	const index = Math.floor(Math.random() * Object.values(this.playFns.choose).length);
	const choice = Object.keys(this.playFns.choose)[index];
	this.addActionToFront(() => this.choosePrivilege(choice, false));
	this.nextAction();
    }
    
    setCoinFlipResult(choice, flipResult, winningTeam) {
	const result = "The " + (this.homeGame ? this.opposingTeam.name : this.yourTeam.name) + " choose " + choice + ". And the flip is " + flipResult + "! The " + (winningTeam) + " have won the toss!";
	this.setState({result: this.state.result + result}, () => this.nextAction());
    }

    choosePrivilege(choice, yourTeam = true) {
	if (choice === "Receive" || choice === "Defer") {
	    delete this.playFns.choose.Receive;
	    delete this.playFns.choose.Defer;
	} else {
	    delete this.playFns.choose.Right;
	    delete this.playFns.choose.Left;
	}
	if (Object.values(this.playFns.choose).length > 0 && yourTeam) {
	    this.addActionToFront(() => this.chooseForOtherTeam());
	}
	this.addActionToFront(() => this.setChoiceResult(choice, yourTeam));
	let possession = this.state.possession;
	let driveTowards = this.state.driveTowards;
	if (choice === "Receive") {
	    if (yourTeam) {
		possession = false;
	    } else {
		possession = true;
	    }
	} else if (choice === "Defer") {
	    if (yourTeam) {
		possession = true;
	    } else {
		possession = false;
	    }
	} else if (choice === "Right") {
	    driveTowards = false;
	} else if (choice === "Left") {
	    driveTowards = true;
	}

	this.setState({
	    possession,
	    driveTowards,
	    quarter: Object.values(this.playFns.choose).length === 0 ? 1 : this.state.quarter,
	},
		      () => {
			  this.possessionAfterHalfTime = !this.state.possession;
			  this.driveTowards = !this.state.driveTowards;
			  this.nextAction();
		      }
	);
    }

    setChoiceResult(choice, yourTeam) {
	const chose = choice === "Receive" ? "receive and will start with the ball in the first half." :
		      choice === "Defer" ? "defer and will kick the ball off to start the game." :
		      choice === "Right" ? "start on the right side of the field." :
		      "start on the left side of the field.";
	const result = " The " + (yourTeam ? this.yourTeam.name : this.opposingTeam.name) + " chose to " +  chose;
	this.setState({result: this.state.result + result});
    }
    
    pickPlay = (yourPlay) => {
	const opposingPlay =  this.pickOpposingPlay();
	const play = this.state.possession ? yourPlay : opposingPlay;
	let fn = null;
	if (yourPlay === "kickOff") {
	    fn = () => this.kickOff();
	} else if (play === "fieldGoal") {
	    fn = () => this.fieldGoal();
	} else if (play === "punt") {
	    fn = () => this.punt();
	} else if (play === "pass") {
	    fn = () => this.pass();
	} else if (play === "run") {
	    fn = () => this.run();
	}
	this.addActionToBack(fn);
    }

    pickOpposingPlay() {
	if (this.state.down === 4) {
	    if (100 - this.state.lineOfScrimmage < 35) {
		return "fieldGoal";
	    } else {
		return "punt";
	    }
	}
	if (Math.random() < .5) {
	    return "pass";
	}
	return "run";
    }
    
    afterPlay(yards) {
	this.addActionToBack(() => this.setYards(yards));
	this.addActionToBack(() => this.checkYards());
	this.addActionToBack(() => this.setDowns());
	this.addActionToBack(() => this.checkDowns());
	this.addActionToBack(() => this.timeRunOff(Math.floor(Math.random() * 5) + 4, true));
    }

    setYards(yards) {
	this.setState(
	    {lineOfScrimmage: yards + this.state.lineOfScrimmage},
	    () => this.nextAction()
	);
    }

    checkYards() {	
	if (this.state.lineOfScrimmage >= 100) {
	    this.addActionToFront(() => this.touchdown());
	    this.addActionToFront(() => this.firstDown());
	} else if (this.state.lineOfScrimmage >= this.state.firstDownLine) {
	    this.addActionToFront(() => this.firstDown());
	}
	this.nextAction();
    }

    setDowns() {
	this.setState(
	    {down: this.state.down + 1},
	    () => this.nextAction()
	);
    }

    checkDowns() {
	if (this.state.down > 4) {
	    this.addActionToFront(() => this.turnoverOnDowns());
	}
	this.nextAction();
    }

    timeRunOff(playLength, clockStop) {
	if (clockStop) {
	    playLength += Math.floor(Math.random()) * 10 + 25;
	}
	if (this.state.time - playLength <= 0) {
	    this.nextQuarter();
	    return;
	}
	this.setState({time: this.state.time - playLength})
    }
    
    firstDown() {
	this.setState({
	    lineOfScrimmage: this.state.lineOfScrimmage,
	    firstDownLine: this.state.lineOfScrimmage + 10,
	    down: 1,
	}, this.nextAction());
    }
    
    turnoverOnDowns() {
	this.addActionToFront(() => this.changePossession());
	const result = " Turnover on downs!  It will be the " + (this.state.possession ? this.opposingTeam.name : this.yourTeam.name) + "'s ball starting on " + this.getYard(100 - this.state.lineOfScrimmage) + " yard line!";
	this.setState(
	    {result: this.state.result + result},
	    () => this.nextAction()
	);
    }

    changePossession() {
	this.setState({
	    down: 1,
	    possession: !this.state.possession,
	    lineOfScrimmage: 100 - this.state.lineOfScrimmage,
	    firstDownLine: 110 - this.state.lineOfScrimmage,
	    driveTowards: !this.state.driveTowards,
	}, () => {
	    this.nextAction();
	});
    }

    setScore(point) {
	this.setState({
	    yourScore: this.state.possession ? this.state.yourScore + point : this.state.yourScore,
	    opposingScore: !this.state.possession ? this.state.opposingScore + point : this.state.opposingScore,
	}, () => this.nextAction());	
    }

    pass(passType = "Medium") {
	const maxYards = 100 - this.state.lineOfScrimmage;
	const options = this.createOptions();
	const defenders = this.createMatchups(options);
	const seperations = [];
	for (let i = 0; i < options.length; i++) {
	    seperations.push(Math.random() * options[i][passType + " Route Running"] -
			     Math.random() * defenders[i]["Man Coverage"]);
	}
	let caught = false;
	let index = 0;
	if (Math.random() > .9) {
	    index = seperations.indexOf(Math.max(...seperations));
	    if (Math.random() * options[index]["Catch in Traffic"] >
		Math.random() * (defenders[index]["Jumping"] + defenders[index]["Awareness"]) / 2 * .8) {
		caught = true;
	    }
	} else {
	    for (let i = 0; i < seperations.length; i++) {	    
		if (seperations[i] <= 0) {
		    if (Math.random() < .2) {
			index = i;
			if (Math.random() * options[i]["Catch in Traffic"] >
			    Math.random() * (defenders[i]["Jumping"] + defenders[i]["Awareness"]) / 2) {
			    caught = true;
			}
			break;
		    }
		} else if (seperations[i] < 35) {
		    if (Math.random() < .4) {
			index = i;
			if (Math.random() * options[i]["Catch In Traffic"] >
			    Math.random() * (defenders[i]["Pursuit"] + defenders[i]["Awareness"]) / 2) {
			    caught = true;
			}
			break;
		    }
		} else {
		    if (Math.random() < .6) {
			index = i;
			if (Math.random() * options[i]["Catching"] >
			    Math.random() * (defenders[i]["Pursuit"] + defenders[i]["Awareness"]) / 2 * .8) {
			    caught = true;
			}
			break;
		    }
		}
	    }
	}
	let yards = caught ? Math.floor(Math.random() * 20) + 5 : 0;
	yards = maxYards < yards ? maxYards : yards;
	this.addActionToBack(() => this.setPassResult(caught, yards, options[index].name, defenders[index].name));
	this.afterPlay(yards);
	this.nextAction();
    }

    setPassResult(caught, yards, offender, defender) {
	const QB = this.state.possession ? this.yourActivePlayers.offense.QB[0] : this.opposingActivePlayers.offense.QB[0];
	let result = QB.name +" passed the ball to "+ offender +"! ";
	if (caught) {
	    result += "It was caught for "+ yards +" yards!";
	} else {
	    result += "It was broken up by "+ defender +"!";
	}
	this.setState({result}, () => this.nextAction());
    }

    run() {
	const maxYards = 100 - this.state.lineOfScrimmage;
	let yards = 0;
	if (Math.random() > .9) {
	    yards = Math.floor(Math.random() * 25) + 15;
	    yards = yards > maxYards ? maxYards : yards;
	} else if (Math.random() > .9) {
	    yards = Math.floor(Math.random() * -3) - 2;
	} else if (Math.random() > .5) {
	    yards = Math.floor(Math.random() * 8) + 5;
	    yards = yards > maxYards ? maxYards : yards;
	} else {
	    yards = Math.floor(Math.random() * 5) + 2;
	    yards = yards > maxYards ? maxYards : yards;
	}
	this.addActionToBack(() => this.setRunResult(yards));
	this.afterPlay(yards);
	this.nextAction();
    }

    setRunResult(yards) {
	const activePlayers = this.state.possession ? this.yourActivePlayers.offense : this.opposingActivePlayers.offense;
	let result = activePlayers.QB[0].name + " hands the ball off to " + activePlayers.HB[0].name + ". ";
	if (yards > 20) {
	    result += activePlayers.HB[0].name + " finds a big hole and takes off running! He gets a huge gain of " + yards + " yards!";
	} else if (yards <= 0) {
	    result += activePlayers.HB[0].name + " has no space to run and gets tackled before even getting to the line of scrimmage. That will be a loss of " + Math.abs(yards) + " yards!";
	} else if (yards > 5) {
	    result += activePlayers.HB[0].name + " finds a decent hole and gets a good gain of " + yards + " yards!";
	} else {
	    result += activePlayers.HB[0].name + " finds a small hole and is able to get " + yards + " yards!";
	}
	this.setState({result}, () => this.nextAction());
    }

    touchdown(result = "") {
	this.addActionToFront(() => this.setKickOff());
	this.addActionToFront(() => this.extraPoint());
	this.addActionToFront(() => this.setScore(6));
	this.addActionToFront(() => this.setTouchDownResult(result));
	this.nextAction();
    }
    
    setTouchDownResult() {
	this.setState({result: this.state.result + " TOUCHDOWN! "}, () => this.nextAction());
    }

    setKickOff() {
	this.setState(
	    {
		isKickOff: true,
		lineOfScrimmage: 35,
		firstDownLine: 45,
	    },
	    () => this.nextAction()
	);
    }
    
    kickOff() {
	this.kick("kick");
    }

    punt() {
	this.kick("punt");
    }
    
    kick(type) {
	const returner = !this.state.possession ? this.yourActivePlayers.special.KR[0] :
			 this.opposingActivePlayers.special.KR[0];
	const kicker = this.state.possession ? this.yourActivePlayers.special.K[0] :
		       this.opposingActivePlayers.special.K[0];
	const dist = type === "kick" ? Math.floor(Math.random() * 30 + 55) : Math.floor(Math.random() * 20) + 35;
	let startYard = type === "kick" ? 65 - dist : 100 - this.state.lineOfScrimmage - dist;
	let endYard = 25;
	let td = false;
	let returnDist = 0;
	if (startYard > - 2) {
	    if (Math.random() > .95) {
		td = true;
	    } else if (Math.random() > .8) {
		returnDist = Math.floor(Math.random()) * 30 + 40;
	    } else if (Math.random > .7) {
		returnDist = Math.floor(Math.random()) * 10 + 10;
	    } else {
		returnDist = Math.floor(Math.random()) * 20 + 20;
	    }
	    endYard = startYard + returnDist;
	}
	this.addActionToFront(() => this.timeRunOff(Math.floor(Math.random() * 5) + 4, false));
	if (td) {
	    this.addActionToFront(() => this.touchdown());
	}
	this.addActionToFront(() => this.setKickResult(startYard, endYard, dist, returner.name, kicker.name, type, returnDist, td));	
	this.setState({
	    down: 1,
	    possession: !this.state.possession,
	    lineOfScrimmage: endYard,
	    firstDownLine: endYard + 10,
	    isKickOff: false,
	    driveTowards: !this.state.driveTowards,
	}, () => this.nextAction());
    }

    setKickResult(startYard, endYard, dist, returnerName, kickerName, type, returnDist, td) {
	let result = kickerName + " " + type + "s the ball off! ";
	if (startYard < -10) {
	    result += "It's a long one for " + dist + " yards and goes sailing out the back of the endzone for a touchback. ";
	} else if (startYard < -2) {
	    result += "It goes " + dist + " yards into the endzone, " + returnerName + " kneels it down for a touchback. ";
	} else {
	    result += "It's a returnable kick that goes for " + dist + " yards! " + returnerName + " catches it and brings it out! " + returnerName;
	    if (td) {
		result += " finds a huge hole! The 20! The 10! ";
	    } else if (returnDist > 35) {
		result += " manages to find a crease and gets a huge gain of " + returnDist + " yards! ";
	    } else if (returnDist < 20) {
		result += " is locked up by the defense quickly! He gets a small gain of " + returnDist + " yards! ";
	    } else {
		result += " gets a decent return of " + returnDist + " yards! ";
	    }
	}
	if (!td) {
	    result += "The " + (this.state.possession ? this.yourTeam.name : this.opposingTeam.name) + " will start their drive on " + (this.getYard(endYard)) + " yard line!";
	}
	this.setState({result}, () => this.nextAction());
    }

    extraPoint() {
	const kicker = this.state.possession ? this.yourActivePlayers.special.K[0] :
		       this.opposingActivePlayers.special.K[0];
	const yard = 15;
	const made = yard + 15 <= 65 && kicker["Kick Power"] >= ((Math.random() * .5) + ((yard + 15) / 100)) * 100 ? true : false;
	this.addActionToFront(() => this.setExtraPointResult(kicker.name, made, yard));
	this.addActionToFront(() => this.setScore(1));
	this.nextAction();
    }

    setExtraPointResult(name, made, yard) {
	const result = " " + name + (made ? " made" : " missed") + " the extra point from the " + yard + " yard line!";
	this.setState({result: this.state.result + result}, () => this.nextAction());
    }
	    
    fieldGoal() {
	const yard = 115 - this.state.lineOfScrimmage;
	const kicker = this.state.possession ? this.yourActivePlayers.special.K[0] :
		       this.opposingActivePlayers.special.K[0];
	const made = yard <= 65 && kicker["Kick Power"] >= ((Math.random() * .5) + (yard / 100)) * 100 ? true : false;
	if (made) {
	    this.addActionToFront(() => this.setKickOff());
	    this.addActionToFront(() => this.setScore(3));
	}
	this.addActionToFront(() => this.setFieldGoalResult(made, yard, kicker.name));
	if (!made) {
	    this.addActionToFront(() => this.changePossession());
	}
	this.nextAction();
    }

    setFieldGoalResult(made, yard, kickerName) {
	const result = made ?
		       " " + kickerName + " made the " + yard + " yard field goal!" :
		       " " + kickerName + " missed the " + yard + " yard field goal!";
	this.setState({result}, () => this.nextAction());
    }
    
    nextQuarter() {
	if (this.state.quarter === 1) {
	    this.addActionToBack(() => this.endOfFirstQuarter()); 
	} else if (this.state.quarter === 2) {
	    this.addActionToBack(() => this.endOfSecondQuarter());
	} else if (this.state.quarter === 3) {
	    this.addActionToBack(() => this.endOfThirdQuarter());
	} else if (this.state.quarter === 4) {
	    this.addActionToBack(() => this.endOfFourthQuarter());
	}
	this.nextAction();
    }

    endOfFirstQuarter() {
	const result = " Well folks that'll be it for the " + Ordinals[this.state.quarter - 1] + " quarter. What a good game of football and we are just getting started. We will be right back after these messages."
	this.setState({
	    result: this.state.result + result,
	    quarter: 2,
	    time: 300,
	});
    }
    
    endOfSecondQuarter() {
	let result = " Well folks that'll be it for the " + Ordinals[this.state.quarter - 1] + " quarter. We are heading into halftime and boy do we have a show for you. Stay tuned and we will be right back after this fifteen minute, commercial filled break.";
	this.setState({
	    isKickOff: true,
	    result: this.state.result + result,
	    quarter: 3,
	    time: 300,
	    down: 1,
	    lineOfScrimmage: 35,
	    firstDownLine: 45,
	    possession: this.possessionAfterHalfTime,
	    driveTowards: false,
	});
    }
    
    endOfThirdQuarter() {
	const result = " Well folks that'll be it for the " + Ordinals[this.state.quarter - 1] + " quarter. And, boy do we have a game on our hands here. So much to look forward to in the fourth and final quarter.";
	this.setState({
	    result: this.state.result + result,
	    quarter: 4,
	    time: 300,
	});
    }
    
    endOfFourthQuarter() {
	const end = this.state.yourScore === this.state.opposingScore ? "game being tied." : (this.state.yourScore > this.state.opposingScore ? this.yourTeam.name : this.opposingTeam.name) + " coming away with the win."
	const result = " Well folks that'll be it for the " + Ordinals[this.state.quarter - 1] + " quarter. What a game we had here tonight, with the " + end + " I'm Chris Collinsworth and I will be signing off for tonight.";
	this.setState({
	    result: this.state.result + result,
	    quarter: 5,
	    time: 0,
	});
    }    
        
    endGame() {
	if (this.state.yourScore === this.state.opposingScore) {
	    globe.setYourResult(0);
	} else if (this.state.yourScore > this.state.opposingScore) {
	    globe.setYourResult(1);
	} else {
	    globe.setYourResult(2);
	}
	globe.changeBoard("Main Page");
    }

    createMatchups(options) {
	const LB = ["ROLB", "LOLB", "MLB"];
	const matchUp = [];
	const defensivePlayers = this.getPossession()[1];
	const lineBackers = LB.map(pos => defensivePlayers[pos][0])
			      .sort((a, b) => b["Man Coverage"] - a["Man Coverage"]);
	for (let player of options) {
	    if (player.position === "WR") {
		for (let player of defensivePlayers.CB) {
		    if (matchUp.includes(player)) {
			continue;
		    }
		    matchUp.push(player);
		}
	    } else if (player.position === "TE" || player.position === "HB") {
		matchUp.push(lineBackers.shift());
	    }
	}
	return matchUp;
    }
    
    createOptions() {
	const offensiveOptions = [
	    "WR",
	    "TE",
	    "HB",
	];
	const options = [];
	const offensivePlayers = this.getPossession()[0];
	for (let pos of offensiveOptions) {
	    for (let player of offensivePlayers[pos]) {
		options.push(player);
	    }
	}
	return options;
    }

    getPossession() {
	return this.state.possession ?
	       [this.yourActivePlayers.offense, this.opposingActivePlayers.defense] :
	       [this.opposingActivePlayers.offense, this.yourActivePlayers.defense];
    }
    
    getYard(los = this.state.lineOfScrimmage) {
	return (los < 50 ? "their own " : "the opponent's ") + 
 	       (los < 50 ? los : 50 - los % 50);
    }

    getTime() {
	let secs = (this.state.time % 60).toString();
	if (secs.length === 1) {
	    secs = "0" + secs;
	}	
	return Math.floor(this.state.time / 60) + ":" + secs;
    }

    render() {
	const footballFieldLos = this.state.driveTowards ? this.state.lineOfScrimmage : 100 - this.state.lineOfScrimmage;
	const footballFieldFdl = this.state.driveTowards ? this.state.firstDownLine : 100 - this.state.firstDownLine;
	const quarter = this.state.quarter < 1 ? 1 : this.state.quarter <= 4 ? this.state.quarter : 4;
	return (
	    <>
		<ScoreBoard
		    homeGame={this.homeGame}
		    possession={this.state.possession}
		    time={this.getTime()}
		    yourScore={this.state.yourScore}
		    opposingScore={this.state.opposingScore}
		    down={this.state.down}
		    lineOfScrimmage={this.state.lineOfScrimmage}
		    firstDownLine={this.state.firstDownLine > 100 ? 100 : this.state.firstDownLine}
		    quarter={quarter}
		/>
		<FootballField
		    lineOfScrimmage={footballFieldLos}
		    firstDownLine={footballFieldFdl > 100 ? 100 : footballFieldFdl < 0 ? 0 : footballFieldFdl}
		    homeTeamName={this.homeGame ? this.yourTeam.name : this.opposingTeam.name}
		/>
		{this.buttons()}
		<div className='result'>{this.state.result}</div>
	    </>
	);
    }    
}

export default Game;
