import React from 'react';
import './index.css';

const FootballField =  ({lineOfScrimmage, firstDownLine, homeTeamName, oldLineOfScrimmage, oldFirstDownLine}) => {
    firstDownLine = firstDownLine >= 100 ? 100 : firstDownLine;
    firstDownLine = firstDownLine <= 0 ? 0 : firstDownLine;
    const counter = new Array(9).fill(0);
    const teamName = homeTeamName.split(" ").pop();
    return (
	<>
	    <div className="fieldContainer">
		<svg height="10em" width={796}>
		    <rect
			width={64}
			height="10em"
			fill="DarkGreen"
			stroke="white"
			strokeWidth={5}
		    />
		    <text
			x={160}
			y={-26}
			fill="white"
			transform={"rotate(90)"}
			textAnchor="middle"
			fontWeight= "bold"
			fontFamily= "'Istok Web', Helvetica, sans-serif"
		    >
			{teamName}
		    </text>
		    <rect
			width={64}
			height="10em"
			fill="DarkGreen"
			transform={"translate("+ 32 * 22 +")"}
			stroke="white"
			strokeWidth={5}
		    />		
		    <text
			x={-160}
			y={32 * 23 + 12}
			fill="white"
			transform={"rotate(-90)"}
			textAnchor="middle"
			fontWeight= "bold"
			fontFamily= "'Istok Web', Helvetica, sans-serif"
		    >
			{teamName}
		    </text>
		    <rect
			width={32 * 20}
			height="10em"
			fill="ForestGreen"
			transform={"translate(64)"}
			stroke="white"
			strokeWidth={4}
		    />
		    <line
			x1={64}
			x2={32 * 23}
			y1={0}
			y2={0}
			stroke="white"
			strokeWidth={"20em"}
			strokeDasharray="2 30"
		    />
		    <line
			x1={64}
			x2={32 * 22}
			y1={0}
			y2={0}
			stroke="white"
			strokeWidth={8}
			strokeDasharray="1 5"
		    />
		    <line
			x1={64}
			x2={32 * 22}
			y1={120}
			y2={120}
			stroke="white"
			strokeWidth={8}
			strokeDasharray="1 5"
		    />
		    <line
			x1={64}
			x2={32 * 22}
			y1={200}
			y2={200}
			stroke="white"
			strokeWidth={8}
			strokeDasharray="1 5"
		    />
		    <line
			x1={64}
			x2={32 * 22}
			y1={320}
			y2={320}
			strokeWidth={8}
			stroke="white"
			strokeDasharray="1 5"
		    />
		    {counter.map((el, i) => {
			const num = (i + 1) * 10;
			return (
			    <text
				key={"topYard" + num}
				x={i * 64 + 112}
				y={32}
				fill="white"
			    >
				{num < 50 ? num : 50 - num % 50}
			    </text>
			)})}
		    {counter.map((el, i) => {
			const num = (i + 1) * 10;
			return (
			    <text
				key={"bottomYard" + num}
				x={i * -64 - 128}
				y={-288}
				fill="white"
				textAnchor="middle"
				transform="rotate(180)"
			    >
				{num < 50 ? num : 50 - num % 50}
			    </text>
			)})}
		    <rect
			className='animated'
			x={lineOfScrimmage * 6.5 + 64}
			y={0}
			width={2}
			height={320}
			fill={"blue"}
		    />
		    <rect
			className='animated'
			x={firstDownLine * 6.5 + 64}
			y={0}
			width={2}
			height={320}
			fill={"yellow"}
		    />
		</svg>
	    </div>
	</>
    )
};

export default FootballField;
